.main__Cont {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: #000;

    .main__inner {
        font-size: 10vw;
        line-height: 0.85;
        font-family: "Saol";
        color: antiquewhite;
        text-transform: uppercase;
    }
}
//main animation
.main__inner > div {
    opacity: 0;
    transform: translateY(100px);
}