//container
.container {
    max-width: 1200px;
    width: 98%;
    margin: 0 auto;
    // background: rgba(255,255,255,0.4);
}
//root
#main {
    background-color: var(--dark);
}
//cont__title
.cont__title {
    padding: 15vmax 1% 10vmax; 
    font-size: 5vmax;
    font-family: var(--main_font);
    text-transform: uppercase;
    line-height: 0.85;
    color: var(--light);

    em {
        display: block;
    }
}

//layout
#mainCont {
    background-color: var(--dark);
}
//cont__contact
.cont__contact {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: var(--light);
    p {
        font-size: 1.4rem;
        font-family: var(--sub_font);
        margin-bottom: 4vw;
        text-transform: uppercase;
    }
    h3 {
        font-size: 9vw;
        font-family: var(--main_font);
        text-align: center;
        line-height: 1;
        margin-bottom: 6vw;
        text-transform: uppercase;
        em {
            display: block;
        }
    }
    a {
        font-size: 1rem;
        font-family: var(--sub_font);
        text-transform: uppercase;
        border: 1px solid var(--light);
        border-radius: 50px;
        padding: 1vw 4vw; 
    }
}
@media(max-width:600px) {
    .cont__title {
        text-align: center;
        padding-bottom: 10vw;
    }
}
//타이틀 애니메이션
.cont__title {

    strong {
        display: block;
        opacity: 0;
        transform: translateX(10px) translateY(10px);
    }
    em {
        display: block;
        opacity: 0;
        transform: translateX(10px) translateY(10px);
    }
}
// 라이트 버전
.light.cont__title {
    background-color: #fff;
    color: var(--dark);
}
