body {
    background: var(--dark);
}
#loading.loading__active {
    opacity: 1;
    transition: all 1.4s;
}
#loading {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--dark);
    z-index: 99999;
    opacity: 0;
    transition: all 1.6s;
}
.loading__text {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    text-align: center;
    width: 100%;
    height: 100px;
    line-height: 100px;

    span {
        display: inline-block;
        margin: 0 5px;
        color: var(--light);
    }

    // span:nth-child(1) {
    //     filter: blur(0px);
    //     animation: blur-text 1.5s 0s infinite linear alternate;
    // }
    
    // span:nth-child(2) {
    //     filter: blur(0px);
    //     animation: blur-text 1.5s 0.2s infinite linear alternate;
    // }
    // span:nth-child(3) {
    //     filter: blur(0px);
    //     animation: blur-text 1.5s 0.4s infinite linear alternate;
    // }
    // span:nth-child(4) {
    //     filter: blur(0px);
    //     animation: blur-text 1.5s 0.6s infinite linear alternate;
    // }
    // span:nth-child(5) {
    //     filter: blur(0px);
    //     animation: blur-text 1.5s 0.8s infinite linear alternate;
    // }
    
    // span:nth-child(6) {
    //     filter: blur(0px);
    //     animation: blur-text 1.5s 1.0s infinite linear alternate;
    // }
    // span:nth-child(7) {
    //     filter: blur(0px);
    //     animation: blur-text 1.5s 1.2s infinite linear alternate;
    // }
    @for $i from 1 through 7 {
        span:nth-child(#{$i}) {
            filter: blur(0px);
            animation: blur-text 1.5s $i * 0.6s infinite linear alternate;
        }
    }
}
@keyframes blur-text {
    0% {
        filter: blur(0px);
    }
    100% {
        filter: blur(4px);
    }
}

//라이트 모드 
#loading.light {
    background: var(--light);
    span {
        color: var(--dark);
    }
}